@import '../../globals/constants.less';
@import '../../globals/responsive.less';

.header {
  height: @header-height;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .logo_section {
    width: 30%;
  }
  .links_section {
    width: 70%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .header_link {
    color: white;
    text-decoration: none;
    margin-left: 2rem;
    &:hover {
      color: rgb(241,241,241);
    }
  }
  .header_button {
    height: 2.5rem;
    width: 8.75rem;
    background: #4c79ff;
    margin-left: 2rem;
  }
  .desktop({
    width: 92%;
    .menu_icon {
      display: none;
    }
    .links_content {
      display: flex;
      align-items: center;
    }
  });
  .mobile({
    width: 100%;
    padding: 0 7%;
    .menu_icon {
      cursor: pointer;
    }
    .links_content {
      display: none;
    }
  });
}
.header_expanded {
  .mobile({
    background: transparent;
  });
}
.content_expanded {
  .mobile({
    position: absolute;
    top: @header-height;
    left: 0;
    width: 100%;
    background: white;
    display: flex !important;
    flex-wrap: wrap;
    justify-content: flex-end;
    padding: 2rem;
    a.header_link {
      display: block;
      color: #4a4a4a;
      text-align: right;
      font-weight: 500;
      width: 100%;
      margin-bottom: 1.5rem;
      &:hover {
        color: #4a4a4a;
        opacity: 0.85;
      }
    }
    box-shadow: @box-shadow;
  });
}



